@import "https://fonts.googleapis.com/css2?family=VT323&display=swap";
:root {
  --color-primary: wheat;
  --color-secondary: #dec9a2;
  --font-color: #6f6451;
  background-color: var(--color-primary);
  color: var(--font-color);
  font-family: VT323, monospace;
  font-size: 16px;
}

body {
  text-align: center;
  justify-items: center;
  display: grid;
}

.cell {
  background-color: var(--color-secondary);
  border: var(--font-color) dashed 2px;
  width: 95%;
  max-width: 30rem;
  margin: 1rem 0;
  padding: 1rem;
}

.rainbow-text {
  color: #0000;
  background: linear-gradient(to right, #66f, #09f, #0f0, #f39, #66f) 0 0 / 400% 100% text;
  animation: 6s ease-in-out infinite rainbow_animation;
}

@keyframes rainbow_animation {
  0%, 100% {
    background-position: 0 0;
  }

  50% {
    background-position: 100% 0;
  }
}
